<template>
    <div class="container">
      <h1>退款单列表</h1>
      <el-button type="primary" class="addItem" @click="addReceivables()">新增退款单</el-button>
      <div class="tableContainer">
        <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID"
        width="80">
      </el-table-column> 
      <el-table-column
        prop="ref"
        label="退款单号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="rv_money"
        label="应收账款总额">
      </el-table-column>
      <el-table-column
        prop="act_money"
        label="已收总额">
      </el-table-column>
      <el-table-column
        prop="balance_money"
        label="未收总额">
      </el-table-column>
      <el-table-column
        prop="supplier_id"
        label="供应商">
      </el-table-column>
      <el-table-column
        prop="action_time"
        label="收款日期">
      </el-table-column>
      <el-table-column
        prop="action_user_id"
        label="操作人">
      </el-table-column>

      <el-table-column
        prop="create_time"
        label="单据生成日期">
      </el-table-column>
      <el-table-column
        prop="purchases_detail_id"
        label="对应采购单id">
      </el-table-column>
      <el-table-column
        prop="pr_bill_id"
        label="对应退货单">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态">
      </el-table-column>

      <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
              <el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button> 
            </template>
        </el-table-column>


      </el-table>
        
      </div>
    </div>
</template>

<script>
    
  export default {
    name: "warehouse",
    data() {
        return {
          tableData: [/*{
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },
          {
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },*/ 
          ],
          totalSize: 0,
          currentPage: 1,
          pageSize: 20
        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      
      addReceivables() {
        this.$router.push('/receivables/add')
      },
      handleEdit(row) {
        this.$router.push({ path: '/receivables/update',query: row})
      },
      handleDetail(row) {
        this.$router.push({ path: '/receivables/index',query: row})
      },
      handleDelete(row,index) {
        let url = 'http://api.orkowms.me/receivables/'+row.id;
        let that = this;
        this.$confirm('是否确认删除该条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.delete(url).then((res) => {
            if(res.status == 200 || res.status == 204){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
        }).catch(() => {});
      },
       
      initTable() {
        let initList = [];
        let url = "http://api.orkowms.me/receivables?page="+this.currentPage+"&per-page="+this.pageSize;
        
        let that = this;
        let config = {
          headers : { 
            'Content-Type':'application/json;charset=UTF-8'
          },
        }; 
        this.$axios.get(url,config).then((res) => {  
          if(res.data.data){  
            let tasksList = res.data.data ; 
            that.totalSize = parseInt(res.data._meta.totalCount);
            tasksList.forEach((task) => { 
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.ref = task.ref;
              tempTask.rv_money = task.rv_money;
              tempTask.act_money = task.act_money;
              tempTask.balance_money = task.balance_money;
              tempTask.supplier_id = task.supplier_id;
              tempTask.action_time = task.action_time;
              tempTask.action_user_id = task.action_user_id;
              tempTask.create_time = task.create_time;
              tempTask.purchases_detail_id = task.purchases_detail_id;
              tempTask.pr_bill_id = task.pr_bill_id;
              tempTask.status = task.status;
              
              initList.push(tempTask);
            })
            
          }
          that.tableData = initList;  
          
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
    .addItem
      margin 20px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



